.breadcrumbs {
	font-size: 14px;
	@include PReg;
	&__item {
		display: inline-block;
		&:not(:first-child) {
			padding-left: 15px; }
		&:not(:last-child) {
			padding-right: 20px;
			position: relative;
			&:after {
				display: block;
				content: '';
				position: absolute;
				right: 0;
				top: 2px;
				background-image: url('../img/breadcrumb.png');
				height: 10px;
				width: 5px;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center; } } }
	&__link {
		color: $cyan; } }

