.button {
	color: #fff;
	font-size: 15px;
	border-radius: 30px;
	padding: 14px 21px 13px;
	@include PSemi;
	display: inline-block;
	@include trans;
	&_cyan {
		background-color: $cyan; }
	&_border {
		background-color: transparent;
		border: 1px solid $cyan;
		color: $cyan;
		&:hover {
			background-color: $cyan;
			color: #fff; } } }
.btn-pink {
	padding: 10px 15px 11px;
	background-color: $pink;
	border-radius: 20px;
	color: $white;
	font-size: 16px;
	border: none;
	display: inline-block;
	vertical-align: top;
	appearance: none;
	@include PBold;
	text-align: center;
	&--240 {
		min-width: 240px; } }
.btnNext, .btnPrev {
	width: 28px;
	height: 28px;
	background-color: $little-grey;
	cursor: pointer;
	text-align: center;
	border-radius: 50%;
	position: absolute;
	i {
		@include vcenter; }
	svg {
		width: 7px;
		height: 12px;
		fill: #14c1e5; } }
