body {
  @include OSReg;
  @include smooth-font;
  font-size: 16px;
  color: $black; }

a {
  text-decoration: none;
  color: $black; }

h1 {
  font-size: 50px;
  @include PSemi; }

h2 {
  font-size: 35px;
  @include PSemi; }

h3 {
  font-size: 30px;
  @include PSemi; }

h4 {
  font-size: 20px;
  @include PSemi; }

.container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 40px; }

.text {
	line-height: 24px; }

.list {
	line-height: 24px;
	margin: 20px 0;
	&__item {
		position: relative;
		padding-left: 15px;
		&:before {
			font-size: 23px;
			display: block;
			position: absolute;
			content: '•';
			color: $cyan;
			top: 0;
			left: 0; } } }

