/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  font-family: OpenSans-Regular, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: #1a1a1a;
}

a {
  text-decoration: none;
  color: #1a1a1a;
}

h1 {
  font-size: 50px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h2 {
  font-size: 35px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h3 {
  font-size: 30px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

h4 {
  font-size: 20px;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

.container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 40px;
}

.text {
  line-height: 24px;
}

.list {
  line-height: 24px;
  margin: 20px 0;
}

.list__item {
  position: relative;
  padding-left: 15px;
}

.list__item:before {
  font-size: 23px;
  display: block;
  position: absolute;
  content: '•';
  color: #14c1e5;
  top: 0;
  left: 0;
}

.faq-section__inner {
  max-width: 818px;
}

.faq-section__title {
  margin-bottom: 12px;
}

.faq-item {
  padding-left: 37px;
  margin-bottom: 20px;
}

.faq-item__head {
  position: relative;
  width: 780px;
  min-height: 40px;
  background-color: rgba(208, 243, 250, 0.6);
  border-radius: 10px;
  padding: 9px 40px 9px 35px;
}

.faq-item__icon {
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translate(-50%, -50%);
  display: block;
  font-size: 0;
  width: 50px;
  height: 50px;
  background-color: #14c1e5;
  border-radius: 50%;
  box-shadow: 0 4px 27px rgba(20, 193, 229, 0.47);
  z-index: 1;
}

.faq-item__icon img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.faq-item__head-text {
  font-size: 16px;
}

.faq-item__arrow {
  position: absolute;
  top: 50%;
  margin-top: -3px;
  transform-origin: 50% 50%;
  right: 22px;
  font-size: 0;
  display: block;
  transition: all 0.2s ease;
  z-index: 1;
}

.faq-item__arrow svg {
  width: 13px;
  height: 7px;
  fill: #14c1e5;
  font-size: 7px;
}

.faq-item__content {
  padding-left: 36px;
  padding-right: 15px;
  padding-top: 8px;
  font-size: 14px;
}

.faq-item__close {
  background: transparent;
  min-width: 73px;
  padding: 5px 5px 6px;
  border: 1px solid #d7d7d7;
  border-radius: 15px;
  appearance: none;
  display: inline-block;
  vertical-align: top;
  margin-top: 30px;
  font-size: 14px;
  font-family: Panton-Bold, Arial, sans-serif;
  color: #d7d7d7;
}

.faq-item:not(.faq-item--opened) .faq-item__content {
  display: none;
}

.faq-item:not(.faq-item--opened) .faq-item__head {
  cursor: pointer;
  user-select: none;
}

.faq-item--opened {
  margin-bottom: 35px;
}

.faq-item.is-open .faq-item__arrow {
  transform: rotateX(180deg);
}

.reviews-slider {
  width: 280px;
  margin: 0 auto 5px;
}

.reviews-slider__content {
  margin-bottom: 22px;
}

.reviews-slider__count {
  padding: 0 40px;
  color: #d9dbdc;
  font-size: 12px;
  text-align: center;
  line-height: 1;
}

.reviews-slider__count-main {
  color: #14c1e5;
  font-family: OpenSans-SemiBold, Arial, sans-serif;
  font-size: 14px;
  margin-bottom: 3px;
}

.reviews-slider .btnNext, .reviews-slider .btnPrev {
  bottom: -19px;
  transform: translateY(100%);
}

.reviews-slider .btnNext {
  right: 0;
}

.reviews-slider .btnPrev {
  left: 0;
}

.review {
  width: 280px;
  margin: 0 auto;
  background-color: #14c1e5;
  border-radius: 5px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
  padding: 16px 20px 20px;
  color: #ffffff;
}

.review__head {
  position: relative;
  padding-bottom: 11px;
  border-bottom: 1px solid #5bd4ed;
  margin-bottom: 13px;
}

.review__head-text {
  color: #ffffff;
  font-family: Panton-SemiBold, Arial, sans-serif;
  font-size: 20px;
  margin-bottom: 5px;
}

.review__head-inner:after {
  content: " ";
  display: table;
  clear: both;
}

.review__stars {
  float: left;
  font-size: 0;
  padding-top: 5px;
}

.review__stars .review__star {
  margin-right: 4px;
}

.review__stars .review__star:last-of-type {
  margin-right: 0;
}

.review__star svg {
  font-size: 20px;
  fill: rgba(255, 255, 255, 0.3);
}

.review__star.is-color svg {
  fill: #ffffff;
}

.review__rate {
  float: right;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 11px;
  font-family: Panton-Bold, Arial, sans-serif;
}

.review__rate span {
  font-size: 24px;
  font-family: Panton-Black, Arial, sans-serif;
}

.review__count-block {
  font-size: 0;
  margin-bottom: 16px;
}

.review__bought, .review__glad {
  display: inline-block;
  vertical-align: top;
}

.review__bought div, .review__glad div {
  font-family: Panton-ExtraBold, Arial, sans-serif;
  font-size: 20px;
  line-height: 1;
}

.review__bought div span, .review__glad div span {
  display: inline-block;
  vertical-align: top;
}

.review__bought i, .review__glad i {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
}

.review__bought p, .review__glad p {
  font-family: Panton-ExtraBold, Arial, sans-serif;
  font-size: 12px;
  line-height: 1;
}

.review__bought svg, .review__glad svg {
  height: 16px;
}

.review__separator {
  display: inline-block;
  vertical-align: top;
  font-size: 35px;
  margin: 0 16px;
  line-height: 1;
}

.review__info {
  font-family: OpenSans-SemiBold, Arial, sans-serif;
  font-size: 13px;
}

.review__text {
  margin-bottom: 7px;
  font-size: 13px;
  line-height: 1.55;
}

.review__person {
  margin-bottom: 17px;
}

.review__person span {
  font-family: OpenSans-SemiBold, Arial, sans-serif;
  font-size: 13px;
}

.review__person p {
  color: #defbfe;
  font-size: 17px;
  font-family: Panton-Bold, Arial, sans-serif;
}

.review__link {
  display: block;
  width: 100%;
}

