  display: block;
  content: '';
  position: absolute;
  right: 0;
  top: 2px;
  background-image: url("../img/breadcrumb.png");
  height: 10px;
  width: 5px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.breadcrumbs__link {
  color: #14c1e5;
}

.rel-posts__title {
  margin-bottom: 25px;
}

.rel-posts__inner {
  margin-bottom: 25px;
}

.rel-posts__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.rel-posts__inner .post-block {
  float: left;
  width: calc(25% - 20px);
  margin-right: 20px;
}

.rel-posts__inner .post-block:last-child {
  margin-right: 0;
}

.rel-posts__button {
  text-align: center;
}

.post-block {
  background-color: #fafafa;
  padding: 20px;
  border-radius: 3px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
}

.post-block:hover {
  box-shadow: 0 4px 16px rgba(20, 193, 229, 0.24);
}

.post-block:hover .post-block__title {
  color: #14c1e5;
}

.post-block__img {
  height: 155px;
  margin-bottom: 14px;
}

.post-block__img img {
  max-width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.post-block__title {
  font-size: 17px;
  transition: all 0.2s ease;
  font-family: Panton-SemiBold, Arial, sans-serif;
}

.post-block__text {
  font-size: 13px;
  line-height: 1.46;
  margin-bottom: 20px;
}

.post-block__meta {
  padding-top: 20px;
  border-top: 1px solid #e7eced;
}

.meta-tag {
  position: relative;
  padding-left: 21px;
  font-size: 13px;
  line-height: 1;
  font-family: Panton-Regular, Arial, sans-serif;
  text-transform: uppercase;
}

.meta-tag__circle {
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
}

.button {
  color: #fff;
  font-size: 15px;
  border-radius: 30px;
  padding: 14px 21px 13px;
  font-family: Panton-SemiBold, Arial, sans-serif;
  display: inline-block;
  transition: all 0.2s ease;
}

.button_cyan {
  background-color: #14c1e5;
}

.button_border {
  background-color: transparent;
  border: 1px solid #14c1e5;
  color: #14c1e5;
}

.button_border:hover {
  background-color: #14c1e5;
  color: #fff;
}

.btn-pink {
  padding: 10px 15px 11px;
  background-color: #ff016e;
  border-radius: 20px;
  color: #ffffff;
  font-size: 16px;
  border: none;
  display: inline-block;
  vertical-align: top;
  appearance: none;
  font-family: Panton-Bold, Arial, sans-serif;
  text-align: center;
}

.btn-pink--240 {
  min-width: 240px;
}

.btnNext, .btnPrev {
  width: 28px;
  height: 28px;
  background-color: #f6f9f9;
  cursor: pointer;
  text-align: center;
  border-radius: 50%;
  position: absolute;
}

.btnNext i, .btnPrev i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btnNext svg, .btnPrev svg {
  width: 7px;
  height: 12px;
  fill: #14c1e5;
}
