.reviews-slider {
	width: 280px;
	margin: 0 auto 5px;
	&__content {
		margin-bottom: 22px; }
	&__count {
		padding: 0 40px;
		color: #d9dbdc;
		font-size: 12px;
		text-align: center;
		line-height: 1; }
	&__count-main {
		color: $cyan;
		@include OSSemi;
		font-size: 14px;
		margin-bottom: 3px; }

	.btnNext, .btnPrev {
		bottom: -19px;
		transform: translateY(100%); }
	.btnNext {
		right: 0; }
	.btnPrev {
		left: 0; } }

.review {
	width: 280px;
	// display: inline-block
	// vertical-align: top
	margin: 0 auto;
	background-color: $cyan;
	border-radius: 5px;
	box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
	padding: 16px 20px 20px;
	color: $white;
	&__head {
		position: relative;
		padding-bottom: 11px;
		border-bottom: 1px solid #5bd4ed;
		margin-bottom: 13px; }
	&__head-text {
		color: $white;
		@include PSemi;
		font-size: 20px;
		margin-bottom: 5px; }
	&__head-inner {
		@include clr; }
	&__stars {
		float: left;
		font-size: 0;
		padding-top: 5px;
		.review__star {
			margin-right: 4px;
			&:last-of-type {
				margin-right: 0; } } }
	&__star {
		svg {
			font-size: 20px;
			fill: rgba($white, 0.3); }
		&.is-color {
			svg {
				fill: $white; } } }
	&__rate {
		float: right;
		color: $white;
		text-transform: uppercase;
		font-size: 11px;
		@include PBold;

		span {
			font-size: 24px;
			@include PBlack; } }
	&__count-block {
		font-size: 0;
		margin-bottom: 16px; }
	&__bought, 	&__glad {
		display: inline-block;
		vertical-align: top;
		div {
			@include PExtra;
			font-size: 20px;

			line-height: 1;
			span {
				display: inline-block;
				vertical-align: top; } }

		i {
			display: inline-block;
			vertical-align: top;
			font-size: 0; }
		p {
			@include PExtra;
			font-size: 12px;
			line-height: 1; }
		svg {

			height: 16px; } }
	&__bought {
		i {} }
	&__separator {
		display: inline-block;
		vertical-align: top;
		font-size: 35px;
		margin: 0 16px;
		line-height: 1; }
	&__glad {}
	&__info {
		@include OSSemi;
		font-size: 13px; }
	&__text {
		margin-bottom: 7px;
		font-size: 13px;
		line-height: 1.55; }
	&__person {
		margin-bottom: 17px;
		span {
			@include OSSemi;
			font-size: 13px; }
		p {
			color: #defbfe;
			font-size: 17px;
			@include PBold; } }
	&__link {
		display: block;
		width: 100%; } }



